.main-nav__toggle {
  display: block;
  margin: 0;
  padding: 0;
  border: none;
  background-color: transparent;
  font-size: 0;
  position: absolute;
  right: 20px;
  top: 32px;
  width: 40px;
  height: 4px;
  font-size: 0;
  background-color: #ffffff;
  outline: none; }
  .main-nav__toggle--close {
    right: 30px;
    width: 4px;
    height: 4px;
    background-color: transparent; }
  .main-nav__toggle::before, .main-nav__toggle::after {
    content: "";
    position: absolute;
    left: 0;
    width: 40px;
    height: 4px;
    background-color: #ffffff;
    transition: ease 0.1s transform; }
  .main-nav__toggle--close::before, .main-nav__toggle--close::after {
    left: -20px;
    width: 30px;
    height: 4px; }
  .main-nav__toggle::before {
    top: -9px; }
  .main-nav__toggle::after {
    top: 9px; }
  .main-nav__toggle--close::before {
