.map {
  position: relative;
  overflow: hidden;
}

.hidden {
  display: none;
}

.map__wrapper {
  position: relative;
  margin-top: -440px;

  &::before {
    content: "";
    position: absolute;
    bottom: 70px;
    left: 50%;
    margin-left: -18px;
    width: 36px;
    height: 36px;
    border-radius: 50%;
    border: 10px solid $white;
    background-color: $lipstick;
  }
}

.map__picture {
  width: 100%;
}

.map__iframe iframe {
  width: 100%;
}

@media (min-width: $tablet) {
  .map__wrapper {
    margin-top: -250px;

    &::before {
      bottom: 208px;
    }
  }
}

@media (min-width: $desktop) {
  .map__wrapper {
    margin-top: -200px;

    &::before {
      bottom: 260px;
    }
  }
}
