.page-footer__inner {
  display: flex;
  justify-content: center;
  padding: 35px 0;
}

@media (min-width: $tablet) {
  .page-footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
  }
}

@media (min-width: $desktop) {
  .page-footer {
    padding: 0 130px;
  }
}
